import { Fragment } from 'react';
import { getTickets, getAverageVelocity } from '../reducers/reducers';
import { connect } from 'react-redux';
import Input from './Input';
import { addTicket, delTicket, averageVelocity } from '../actions/estimationActions';

const OuterForm = ({ tickets, dispatch }) => {
    return (
        <Fragment>
            <div>
                <h2>Input</h2>
                <div>
                    {tickets}
                </div>
                <button className="btn btn-primary" onClick={() => dispatch(delTicket(tickets.length - 1))}>-</button>
                <button className="btn btn-primary" onClick={() => dispatch(addTicket(<Input key={tickets.length}/>))}>+</button>
            </div>
            <div>
                <input onChange={(e) => averageVelocity(e.target.value, e.target.id)} id="average-cyle-velocity" type="number" name="average-cyle-velocity" placeholder="Average cycle velocity"></input>
                <p>Average Weekly Velocity {averageVelocity}</p>
            </div>
        </Fragment>
    );
};

const mapStateToProps = state => ({
    tickets: getTickets(state),
    averageVelocity: getAverageVelocity(state)
});

export default connect(
    mapStateToProps,
)(OuterForm);
